.bodyHomeSuggestMovie {
  display: flex;
  width: calc(1300px/7);
  flex-direction: column;
  align-items: center;
  margin: 0 1px;
}

.bodyHomeSuggestMovie img{
  height: 250px;
  object-fit: contain;
  display: block;
  outline: 7px solid rgba(255, 255, 255, 0.25);
  outline-offset: -7px;
}
